body {
  position: relative;
  font-family: 'Source Sans Pro', sans-serif;
  color: #616161; }

.header {
  z-index: 2;
  position: sticky;
  top: 0;
  width: 100%;
  background-color: #fff;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.05); }
  .header > div {
    max-width: 1600px;
    margin: 0 auto;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    padding: 10px 40px; }
    @media only screen and (max-width: 820px) {
      .header > div {
        padding: 10px 10px; } }
  .header__logo img {
    height: 36px; }
  .header__user {
    display: flex;
    justify-content: center;
    align-items: center; }
    .header__user__name {
      font-size: 18px;
      margin-right: 18px; }
    .header__user__points {
      padding: 10px;
      justify-content: center;
      align-items: center;
      background: #EDEDED;
      border-radius: 20.5px;
      box-sizing: border-box;
      height: 48px;
      min-width: 100px;
      display: flex;
      font-size: 18px; }
      .header__user__points img {
        margin-left: 5px;
        margin-bottom: -5px;
        height: 24px; }
