.banner {
  max-width: 1600px;
  margin: 0 auto;
  box-sizing: border-box;
  padding: 50px 100px;
  height: 400px;
  background-image: url("/static/img/header.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  align-items: flex-end; }
  @media only screen and (max-width: 820px) {
    .banner {
      padding: 30px 20px;
      height: 100px;
      font-size: 14px; } }
  .banner__title {
    font-size: 64px;
    font-weight: 400;
    color: #fff;
    margin: 0; }
    @media only screen and (max-width: 820px) {
      .banner__title {
        font-size: 34px; } }
